import React from 'react'
import Link from 'gatsby-link'
import Helmet from "react-helmet";
import PostListItem from '../../../components/PostListItem'

const Rethinkdb = ( { data } ) => (
  <div className="container card">
  <Helmet title={ 'RethinkDB - ' + data.site.siteMetadata.title } />
  <div className="row">
      <div className="col-12">
        <h1 className="center">Showing contents of: RethinkDB</h1>
        <hr className="separator"/>
      </div>
    </div>
    <div className="row">
      <div className="col-12">
        <p>RethinkDB is known to be the database for real time web. It pushes JSON data to your clients. RethinkDB is open source.</p>
        <p>In this section you will learn some administrative tasks related to it.</p>
      </div>
    </div>
    <div className="row">
      <div className="col-12">

<ol className="post-list">
      {data.allMarkdownRemark.edges.map(({ node }) =>
        <PostListItem 
        id={node.id}
        path={"/journal/"+ node.frontmatter.category +node.frontmatter.path}
        title={node.frontmatter.title}
        date={node.frontmatter.date}
        foreword={node.frontmatter.foreword}
      />
      )}
</ol>
      </div>
    </div>
  </div>
)

export default Rethinkdb

export const Rethinkdbquery = graphql
`query RethinkdbQuery {
  site {
    siteMetadata {
      title
    }
  }
  allMarkdownRemark(sort: {order: DESC, fields: [frontmatter___date]} 
    filter: {frontmatter: { category: { eq: "rethinkdb" } }} ) {
    edges {
      node {
        id
        timeToRead
        frontmatter {
          title
          date(formatString: "MMMM DD, YYYY")
          path
          category
          foreword
        }
        excerpt
      }
    }
  }
}`


